.form_container {
  background-color: var(--orange);
  color: var(--beige);
  position: static;
  overflow: auto;
}

.form_text {
  font-size: 0.8em;
  line-height: 1.2em;
}

.TDAColor {
  margin: 0 auto;
  display: block;
}
.background {

  background-image: url("/src/assets/tache2.png");
  background-size: 100% 100%;
  opacity: 0.7;

}
@media (min-width: 992px) {
  .form_container {
    position: fixed;
    top: 0px;
    bottom: 0px;
    z-index: 1040;
  }
  @media (max-height: 800px) {
    .form_text {
      font-size: 0.8em;
      line-height: 1.15em;
    }
  }
  @media (max-height: 720px) {
    .form_text {
      font-size: 0.7em;
      line-height: 1.1em;
    }
  }
  @media (max-height: 700px) {
    .form_text {
      font-size: 0.65em;
      line-height: 1.1em;
    }
  }
}

.row {
  height: 100%;
  justify-content: space-between;
}
