.link {
  z-index: 5;
  bottom: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
}
.icon {
  padding: 2%;
  transform: rotate(90deg);
}

.bot {
  position: absolute;
  padding: 5%;
}

.top {
  background-color: var(--red) !important;
}
@media (max-width: 576px) {
  .bot {
    display: none !important;
  }
}
