.slide {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.slider {
  height: 100vh;
  width: calc((100vw - 1140px) / 2 + 15px + (1140px * 3 / 4));
}
@media (max-width: 1200px) {
  .slider {
    width: calc((100vw - 960px) / 2 + 15px + (960px * 3 / 4));
  }
}

@media (max-width: 991px) {
  .slider {
    width: 100%;
  }
}
